.orders {
  padding: 1px 0 10px 0;
  position: relative;
  background-color: #b9c9b4;
}
.orders .user_info {
  width: 40%;
  margin: 99px auto;
}
.orders .user_info p {
  font-size: 2rem;
  margin-bottom: 10%;
  padding: 15px 20px;
  background-color: #fafaf2;
  border-radius: 8px;
  text-align: center;
  color: #696767;
}
.orders .user_info .nom-prenom {
  display: flex;
}
.orders .user_info .nom-prenom .mb-3 {
  width: calc(50% - 20px);
}
.orders .user_info .nom-prenom .mb-3:last-child {
  width: 50%;
  margin-left: 20px;
}
.orders .user_orders {
  width: 75%;
  margin: 0 auto 6% auto;
  position: relative;
  background-color: #fafaf2;
  padding: 100px 30px;
  border-radius: 20px;
}
.orders .user_orders label {
  position: absolute;
  background-color: white;
  border: 1px solid rgba(47, 47, 149, 0.3);
  width: 62%;
  font-size: 1.5rem;
  text-align: center;
  border-radius: 8px;
  padding: 25px 25px;
  top: 35px;
  left: 50%;
  margin-left: calc(62% / -2);
  transition: 0.4s;
}
.orders .user_orders label:hover {
  cursor: pointer;
  box-shadow: 0 0 2px 4px rgba(47, 47, 149, 0.3);
}
.orders .user_orders input {
  display: none;
}
.orders .user_orders > button {
  position: absolute;
  width: 150px;
  height: 47px;
  bottom: 20px;
  right: 20px;
  background-color: #59c064;
  border: 0;
  font-size: 1.4rem;
}
.orders .user_orders > button > div {
  position: absolute;
  left: 50%;
  bottom: 0;
  margin-left: calc(40px / -2);
}
.orders .user_orders > button:hover {
  background-color: #428b49;
}
.orders .user_orders > button:active {
  background-color: #428b49;
}
.orders .user_orders .calendar {
  margin-top: 86px;
  position: relative;
}
.orders .user_orders .calendar p {
  font-size: 1.5rem;
  text-align: center;
  font-weight: 700;
}
.orders .user_orders .calendar .example-custom-input {
  position: absolute;
  font-size: 1.4rem;
  width: 280px;
  padding: 20px 30px;
  text-align: center;
  border-radius: 8px;
  background-color: #f5fff2;
  bottom: -86px;
  left: 17vw;
}
.orders .user_orders .calendar .react-datepicker__day--outside-month {
  color: rgba(91, 135, 75, 0.692);
}
@media screen and (max-width: 1024px) {
  .orders .user_info {
    width: 60%;
  }
  .orders .user_info p {
    font-size: 1.5rem;
  }
  .orders .user_orders label {
    font-size: 1.2rem;
    padding: 20px;
    width: 50%;
    margin-left: calc(50% / -2);
  }
  .orders .user_orders svg {
    width: 80px;
  }
  .orders .user_orders .calendar {
    margin-top: 56px;
  }
  .orders .user_orders .calendar p {
    font-size: 1.2rem;
  }
  .orders .user_orders .calendar .example-custom-input {
    left: 3vw;
  }
}
@media screen and (max-width: 767px) {
  .aide {
    display: none;
  }
  .orders .user_info {
    width: 80%;
  }
  .orders .user_info input {
    font-size: 0.8rem;
  }
  .orders .user_orders label {
    font-size: 0.8rem;
    padding: 10px;
    width: 60%;
    margin-left: calc(60% / -2);
  }
  .orders .user_orders svg {
    position: absolute;
    width: 40px;
  }
  .orders .user_orders svg:first-child {
    left: 0;
  }
  .orders .user_orders .calendar {
    margin-top: 26px;
  }
  .orders .user_orders .calendar p {
    font-size: 0.8rem;
  }
  .orders .user_orders .calendar .example-custom-input {
    font-size: 0.7rem;
    width: 120px;
    padding: 10px;
    text-align: center;
    border-radius: 8px;
    background-color: #f5fff2;
    bottom: -86px;
    left: 1%;
  }
  .orders .user_orders > button {
    position: absolute;
    width: 80px;
    height: 27px;
    bottom: 20px;
    right: 20px;
    background-color: #59c064;
    border: 0;
    font-size: 0.7rem;
  }
  .orders .user_orders > button > div {
    position: absolute;
    left: 50%;
    bottom: 70%;
  }
}
